:root{--verticalSpacerSml:24px;--verticalSpacerL:48px;--verticalSpacerXL:64px;--horizontalPadding:15px}@media screen and (min-width:768px){:root{--verticalSpacerSml:32px;--verticalSpacerL:64px;--verticalSpacerXL:80px;--horizontalPadding:32px}}@media screen and (min-width:1024px){:root{--horizontalPadding:61px}}@media screen and (min-width:1920px){:root{--verticalSpacerSml:48px;--verticalSpacerL:64px;--verticalSpacerXL:128px;--horizontalPadding:88px}}.spacer{width:100%;text-align:center}.spacer.sml{height:var(--verticalSpacerSml)}.spacer.l{height:var(--verticalSpacerL)}.spacer.xl{height:var(--verticalSpacerXL)}